.column {
  height: 1122.5px;
  max-height: 1122.5px;
}

.left {
  width: 14%;
  background-color: #F7FCFF;
  border-right: 2px solid lightgray;
}
.middle {
  width: 793.7px;
  background-color: #ffffff;
  
}
.right {
  width: calc(86% - (793.7px));
  background-color: #F7FCFF;
  border-left: 2px solid lightgray;
}
