div.sticky {
    /* position: -webkit-sticky; */
    /* position: sticky; */
    top: 0;
    position: right;
    padding: 5px;
    align: "left";
    display: inline;
    background-color: #fafafa;
    border: 2px solid #979b97;
    width: 450px;
    height: 100px;
  }

  .inputfile {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }